<template>
	<text>{{text}}</text>
</template>

<script>
export default {
	name: 'CountDown',
	props: {
		time: {
			type: Number,
			default: 0
		}
	},
	data () {
		return {
			text: this.time,
			timeTick: null
		}
	},
	watch: {
		text (val) {
			console.log(val)
		}
	},
	mounted () {
		this.timeTick = setInterval(() => {
			this.text --
		}, 1000)
	},
	methods: {
		changeTab ({currentTarget: {id}}) {
			this.active = id
			this.$emit('changeTab', id)
		}
	}
}
</script>

